import MyHeader from '@/components/MyHeader'
import { mapState } from 'vuex'
export default {
  name: 'CreateJson',
  components:{ MyHeader },
  data () {
    return {
      appStr: ''
    }
  },
  computed: {
    ...mapState({
      dataStr:state => JSON.stringify(state)
    })
  },
  methods:{}
}



