import MyHeader from '@/components/MyHeader'
export default {
  name: 'Curve',
  components:{ MyHeader },
  data () {
    return {
      unitStr:{
        day:'',
        month:''
      },
      curve: {
        id:'',
        name:'',
        dotList:[]
      }
    }
  },
  created(){
    this.curve = JSON.parse(this.$route.query.curve)
  }
}



