import { mapGetters } from 'vuex'
export default {
  name: 'Tab2',
  data () {
    return {
      hasTaskDays: [],
      nowYear: (new Date).getFullYear(),
      nowMonth: (new Date).getMonth() + 1,
      dayTaskList:[] // 
    }
  },
  computed: {
    ...mapGetters(['taskDayObj'])
  },
  created(){
    this.hasTaskDays = this.taskDayObj.hasTaskDays
    this.dayTaskList = this.taskDayObj.dayTaskList
  },
  methods:{}
}



