import { mapActions } from 'vuex'
const URL = 'https://haomiaozhichen.github.io/ebhs-review-data.js'
export default {
  name: 'App',
  data () {
    return {}
  },
  computed: {},
  created(){
    this.getWebData()
  },
  methods:{
    ...mapActions(['setStore']),
    async getWebData(){
      this.$http.post(URL).then(({data}) => {
        console.log('reviewTasks.json:', JSON.stringify(data,null,4))
      })
    }
  }
}



