import { mapState } from 'vuex'
export default {
  name: 'Tab4',
  data () {
    return {
      msg: 'Hello World!'
    }
  },
  computed: {
    ...mapState({
      curveList:state=>state.curveList
    })
  },
  methods:{
    addCurve(){
      console.log('addCurve')
    },
    onCurveClick(curve){
      this.$router.push({path:'/curve', query:{curve:JSON.stringify(curve)}})
    }
  }
}



