import MyHeader from '@/components/MyHeader'
import { mapActions } from 'vuex'
export default {
  name: 'importJson',
  components:{ MyHeader },
  data () {
    return {
      jsonStr: ''
    }
  },
  methods:{
    ...mapActions(['setStore']),
    importJson(){
      this.setStore(JSON.parse(this.jsonStr))
    }
  }
}



