import MyHeader from '@/components/MyHeader'

export default {
  name: 'Home',
  components:{ MyHeader },
  data () {
    return {
      tabIdx: 0,
      tabList: [{
        name: '',
        title: '',
        linkPath: '/',
        icon: 'ios-heart',
      }, {
        name: '',
        title: '',
        linkPath: '/tab2',
        icon: 'ios-checkmark',
      }, {
        name: '',
        title: '',
        linkPath: '/tab3',
        icon: 'ios-star',
      }, {
        name: '',
        title: '',
        linkPath: '/tab4',
        icon: 'ios-information'
      }]
    }
  },
  created(){
    let nowFullPath = this.$route.fullPath
    this.tabList.forEach((item, index)=>{
       if(item.linkPath === nowFullPath){
         this.tabIdx = index
       }
    })
  },
  methods:{
    onTouchMove(e){
      e.preventDefault()
    },
    tabInxChange(val){
      this.tabIdx = val
    }
  }
}



